import React, { Fragment } from "react";
import { Button, Space, Typography } from "antd";
import {
  ModalContent,
  ModalOpenButton,
  useModalContext,
} from "components/ui/Modal";
import { Form, FormContentBase } from "components/ui/Form/Form";
import { AccessControlTypes, Feature } from "types";
import BuyLicenseAlertBox from "../ui/BuyLicenseAlertBox";
import useAppStore from "components/context/app/Hooks";

export default function RateLimitSettingsModal() {
  const { licensed, settings } = useAppStore()
  const { onOk, form } = useModalContext();

  function handleOk() {
    onOk("/settings", "update", settings);
  }

  return (
    <Fragment>
      <ModalOpenButton requiredRoles={["Administrator", "Operator"]} requiredAccessControls={AccessControlTypes.Edit} allowedWithOneWayGitSync={false}>
        <Button children="Rate Limit Settings" />
      </ModalOpenButton>
      <ModalContent title="Update rate limit settings" onClickOk={handleOk}>
        <Space direction="vertical" size="large">
          <Typography.Text>
            Server-wide rate limit settings. The various settings in this dialog allow for exclusion from rate limit rules. You can use the wildcard character (*) for pattern matching.
          </Typography.Text>
          <Form schemaName="ratelimitSettings">
            <FormContentBase
              name="create_ratelimit_settings_form"
              form={form}
              preserve={false}
              defaultValues={{
                ...settings
              }}
            />
            {!licensed(Feature.Api) && <BuyLicenseAlertBox />}
          </Form>
        </Space>
      </ModalContent>
    </Fragment>
  );
}
